import React, { useEffect, useState } from 'react'
import { Container, Row } from 'react-bootstrap'
import MoviesCard from '../Component/Movies/MoviesCard'
import Pagination from '../Component/Pagination/Pagination'
import { useDispatch, useSelector } from 'react-redux'
import { getMovies, getPagePagination } from '../Redux/Actions/moviesActions'

const HomePage = () => {
    const dispatch = useDispatch()
    const [pageCount, setPageCont] = useState(0)
    useEffect(() => {
        dispatch(getMovies())
    }, [dispatch]);
    const state = useSelector(state => state.movies)
    const page = useSelector(state => state.pageCount)
    const loading = useSelector(state => state.loading)

    useEffect(() => {
        setPageCont(page)
    }, [page]);

    const handelPage = async (page) => {
        await dispatch(getPagePagination(page))
    }

    return (
        <Container className='homePage '>
            <Row className='disply_row m-0 py-3  '>

                {
                    loading ? (<div className="spinner-border  mx-auto" role="status">
                        <span className="visually-hidden mx-auto">Loading...</span>
                    </div>) : (
                        state ? (state.map((item) => {
                            return (
                                <MoviesCard key={item.id} mov={item} />
                            )
                        })) : (null)
                    )

                }

            </Row>
            <Row className='m-0'>
                {pageCount > 0 && <Pagination pageCount={pageCount} handelPage={handelPage} />}
            </Row>
        </Container>
    )
}

export default HomePage